.store-cont {
  margin: 78px 0 140px;
  h3 {
    margin-bottom: 52px;
    font-size: 22px;
    color: #1f1f1f;
  }
  .search-store {
    margin-bottom: 50px;
    padding: 45px 0 45px 0;
    background-color: #f8f8f8;
    label {
      color: #1f1f1f;
      font-size: 14px;
      display: inline-block;
      padding: 0 20px 0 39px;
    }
    select, input {
      box-sizing: border-box;
      height: 40px;
      padding: 0 10px;
      border: 1px solid #dfdfdf;
    }
    select {
      width: 190px;
    }
    input.name {
      width: 235px;
      margin: 0 12px;
    }
  }
  .store-top-menu {
    position: relative;
    padding-bottom: 30px;
    &::after {
      content: " ";
      display: block;
      height: 0;
      clear: both;
    }
    span {
      font-size: 14px;
      color: #898989;
      strong {
        color: #1f1f1f;
        font-weight: normal;
      }
    }
    .select-wrap {
      float: right;
      &>a {
        display: inline-block;
        color: #000;
        font-size: 13px;
        &:after {
          background: url(/_ui/img/pc/icn/icn-pooduct-option.png) no-repeat;
          content: "";
          display: inline-block;
          height: 5px;
          margin-left: 8px;
          vertical-align: middle;
          width: 7px;
        }
      }
      .store-type {
        ul li {
          .typeOption {
            color: #898989;
            display: block;
            font-size: 12px;
            padding: 10px 0;
            &.active {
              color: #1f1f1f;
            }
          }
        }
      }
    }
  }
  .store-list {
    text-align: center;
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
    border-top: 4px solid #000;
    font-size: 14px;
    th {
      color: #898989;
    }
    thead {
      th {
        height: 38px;
        padding-bottom: 10px;
        vertical-align: bottom;
      }
    }
    tbody {
      th,
      td {
        vertical-align: middle;
        height: 59px;
        padding: 10px;
        border-bottom: 1px solid #f1f1f1;
        word-break: keep-all;
        line-height: 1.3;
        a.btn {
          padding: 0 18px;
          font-size: 12px;
        }
      }
      th {
        font-size: 13px;
      }
    }
  }
  .list-more {
    margin: 60px auto 0;
  }
}
